@import url('https://fonts.googleapis.com/css2?family=Cute+Font&display=swap');

.PairMaker {
  font-family: 'Cute Font', sans-serif;
  color: rgb(72, 72, 72);
  font-size: 18px;
  margin: 20px;
  width: 500px;
  border: 2px solid black;
}

ul,ol {
  list-style: none;
}

button {
  margin-left: 5px;
  width: 50px;
  height: 30px;
  background-color: rgb(194, 148, 234);
  border-style: none;
  border-radius: 5px;
  font-family: 'Cute Font', sans-serif;
  color: rgb(255, 255, 255);
  font-size: 19px;
}
button:hover {
  cursor: pointer;
}

.student-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 0 20px;
}

.student {
  padding: 5px;
  margin: 5px;
  border: 2px solid rgb(138, 138, 138);
  border-radius: 10px;
}
.student:hover {
  cursor: pointer;
  background-color: rgb(187, 187, 187);
  color: white;
}

.pair-list {
  display: block;
  padding: 15px;
  background-color: aliceblue;
  border-radius: 15px;
}
.pair-list li {
  margin-top: 10px;
  font-size: 22px;
}

.random {
  margin: 10px;
  padding: 20px;
  border-top: 3px dotted grey;
}

.random button {
  width: 150px;
  margin: 0 10px;
  font-size: 20px;
}

.the-one {
  font-size: 28px;
  font-weight: 700;
  padding: 15px;
  background-color: aliceblue;
  border-radius: 15px;
}